body {
  background-color: #e6e6e6;
  font-family: "Helvetica-Neue-LTW-Light", "Segoe UI", "Open Sans", "Helvetica Neue", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
}

main {
  overflow: hidden;
}

a:focus,
button:focus {
  outline: 0;
}

a:focus-visible,
button:focus-visible {
  outline: 5px auto #00a0d2;
}

.input-box {
  align-content: flex-start;
  display: flex;
  flex-wrap: wrap;
  font-family: "Helvetica-Neue-LTW-Light", "Segoe UI", "Open Sans", "Helvetica Neue", "Arial", sans-serif;
  min-height: 90px;
  padding: 0 5px;
  width: 100%;
}
.input-box__label {
  flex-basis: 100%;
  font-size: 12px;
  padding: 0 0 5px 10px;
}
.input-box__input-wrap {
  display: flex;
  position: relative;
  width: 100%;
}
.input-box__input-prefix {
  align-items: center;
  background-color: #d7d7d7;
  border-radius: 25px 0 0 25px;
  display: flex;
  font-size: 18px;
  padding: 0 7px 0 15px;
  width: 65px;
}
.input-box__input {
  border: 1px solid #d7d7d7;
  border-radius: 25px;
  box-shadow: inset 0px 1px 3px 0px #d7d7d7;
  display: block;
  font-family: "Helvetica-Neue-LTW-Light", "Segoe UI", "Open Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 18px;
  height: 50px;
  padding: 10px 20px;
  width: 100%;
}
.input-box__input--prefixed {
  border-radius: 0 25px 25px 0;
  padding-left: 7px;
